.material-symbols-outlined {
	vertical-align: sub !important;
}

.wp-block-create-block-meta-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.simplecal_meta_block_row {
	display: flex;
	flex-flow: row nowrap;
	gap: 0.5em;
	align-items: center;
	
	h2, h3 {
		margin: 0;
	}
}

.simplecal_meta_block_row + .simplecal_meta_block_row {
	margin-top: 16px;
}
